@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video, button {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
	font-family: 'Roboto';
	color: #94a1b2;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

html, body, #root, #app {
	height: 100%;
	width: 100%;
	background-color: rgb(28, 28, 30);
}

main {
    height: 100%;
    margin: 0 auto;
}

.loader {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 151px;
    height: 91px;
    font-size: 0;
    margin: 0 auto 0 auto;
}
.loader > .elem {
    display: inline-block;
    vertical-align: middle;
    width: 12px;
    height: 12px;
    background: transparent;
    border-radius: 50px;
    -webkit-animation: anim-loader 2.5s ease-in-out infinite;
            animation: anim-loader 2.5s ease-in-out infinite;
}
.loader > .elem:nth-child(2) {
    margin-left: 12px;
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s;
}
.loader > .elem:nth-child(3) {
    margin-left: 12px;
    -webkit-animation-delay: 0.4s;
            animation-delay: 0.4s;
}
.loader > .elem:nth-child(4) {
    margin-left: 12px;
    -webkit-animation-delay: 0.6s;
            animation-delay: 0.6s;
}
.loader > .elem:nth-child(5) {
    margin-left: 12px;
    -webkit-animation-delay: 0.8s;
            animation-delay: 0.8s;
}
.loader > .elem:nth-child(6) {
    margin-left: 12px;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
}
.loader > .elem:nth-child(7) {
    margin-left: 12px;
    -webkit-animation-delay: 1.2s;
            animation-delay: 1.2s;
}
@-webkit-keyframes anim-loader {
    0% {
        height: 12px;
        box-shadow: none;
   }
    30% {
        height: 50px;
        box-shadow:  inset 5px 5px 7px #111112, inset -5px -5px 7px #27272a;
   }
    60% {
        height: 12px;
        box-shadow: none;
   }
}
@keyframes anim-loader {
    0% {
        height: 12px;
        box-shadow: none;
   }
    30% {
        height: 50px;
        box-shadow:  inset 5px 5px 7px #111112, inset -5px -5px 7px #27272a;
   }
    60% {
        height: 12px;
        box-shadow: none;
   }
}

::-webkit-scrollbar {
    width: 2px;
    height: 2px;
}
::-webkit-scrollbar-button {
    width: 0px;
    height: 0px;
}
::-webkit-scrollbar-thumb {
    background: #e1e1e1;
    border: 0px none #ffffff;
    border-radius: 50px;
}
::-webkit-scrollbar-thumb:hover {
    background: #ffffff;
}
::-webkit-scrollbar-thumb:active {
    background: #000000;
}
::-webkit-scrollbar-track {
    background: rgba(0,0,0,0);
    border: 0px none #ffffff;
    border-radius: 50px;
}
::-webkit-scrollbar-track:hover {
    background: rgba(0,0,0,0);
}
::-webkit-scrollbar-track:active {
    background: #333333;
}
::-webkit-scrollbar-corner {
    background: transparent;
}


#launches {
    height: 100%;
}

.fake-header {
    height: 100px;
}

.month-name {
    text-align: center;
    margin: 30px 0 25px 0;
    color: #FFFFFE;
    font-weight: 700;
    text-transform: capitalize;
    font-size: 1.8em;
}

#launches div.launches-list {
    padding: 0 20px;
    padding-top: 25px;
    min-height: calc(100% - 300px);
    overflow-y: auto;
    overflow-x: hidden;
    display: flex;
    flex-direction: column;
    padding-bottom: 95px;
}
#launches div.launch-item {
    border-radius: 25px;
    margin-left: auto;
    margin-right: auto;
    height: 450px;
    min-height: -webkit-fit-content;
    min-height: -moz-fit-content;
    min-height: fit-content;
    width: -webkit-fill-available;
    width: -moz-available;
    width: stretch;
    max-width: 900px;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    margin-bottom: 30px;
    background-color: rgb(44, 44, 46);
    cursor: pointer;
    transition: 0.3s;
}
#launches div.launch-item:hover {
    transition: 0.3s;
    transform: scale(1.085);
}
.launch-img {
    background-position: top;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 360px;
}
.launch-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 90px;
    background-color: rgb(44, 44, 46);
    box-shadow: 0px -8px 20px -1px rgba(0,0,0,0.2);
    overflow: hidden;
}
.launch-info h2 {
    text-align: center;
    font-weight: 700;
    margin-bottom: 8px;
    font-size: 1.3em;
    color: #FFFFFE;
    width: 70%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.launch-info p {
    font-size: 0.8em;
    margin-bottom: 5px;
}
.launch-info .date {
    text-transform: capitalize;
    color: #FFFFFE;
}
.launch-info .date span {
    font-size: 0.8em;
}
.launch-info a {
    color: #7f5af0;
    font-size: 0.8em;
    text-decoration: none;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}
header {
    height: 80px;
    display: flex;
    flex-direction: row;
    width: 100%;
    width: calc(100% - 20px);
    max-width: 930px;
    margin: 10px auto 0 auto;
}
header div {
    width: calc(100%/2);
    display: flex;
    padding: 0 40px;
    justify-content: flex-start;
}

header div.author-credits {
	font-size: 0.7em;
	opacity: 0.5;
	text-align: center;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
}
.author-credits span {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}
.author-credits span:first-of-type {
    margin-bottom: 5px;
}

h1 {
    font-size: 1.57em;
    display: flex;
    align-items: center;
    justify-content: center;
	font-weight: 700;
    color: #FFFFFE;
    text-align: center;
}


@media only screen and (max-width: 730px) {
    header {
        flex-direction: column;
        justify-content: center;
    }
    header div {
        width: 100%;
        padding: 0;
        justify-content: center;
    }
    header div.author-credits {
        align-items: center;
    }
    header h1 {
        margin-bottom: 12px;
    }
}
.search-container {
    margin: 25px auto 0 auto;
    width: calc(100% - 20px);
    max-width: 800px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
}

.search-container button {
    width: 110px;
    text-align: center;
    cursor: pointer;
    background-color: rgb(44, 44, 46);
    border: none;
    border-radius: 20px;
    outline: none;
    font-size: 0.9em;
    transition: 0.3s;
    color: #FFFFFE;
    margin: 0 5px;
}
.search-container button svg {
    height: 10px;
}
.search-container button img {
    height: 10px;
}
.search-container button img.soyuz {
    height: 15px;
}
.search-container button:hover {
    box-shadow: none;
    background: #242629;
    transition: 0.3s;
    transform: scale(1.1);
}

.search-input-container {
    background-color: rgb(44, 44, 46);
    border-radius: 20px;
    height: 40px;
    color: #FFFFFE;
    display: flex;
    width: calc(100% - 40px);
}

.search-input {
    outline: none;
    padding: 0 20px;
    border: none;
    height: 100%;
    width: calc(100% - 60px);
    border-radius: 20px 0 0 20px;
    background-color: rgba(0,0,0,0);
    color: #FFFFFE;
    font-size: 16px;
}

.search-input-container label,
.search-input-container span {
    height: 100%;
    width: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.search-input-container label svg,
.search-input-container span svg {
    cursor: pointer;
    height: 15px;
    color: #FFFFFE;
}

.filters-container {
    margin-top: 20px;
    height: 40px;
    display: flex;
    flex-direction: row;
}


.card-header {
    height: 200px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    padding: 15px;
    flex-direction: column;
    justify-content: space-between;
}
.card-header button {
    background: none;
    border: none;
    outline: none;
    transition: 0.3s;
}
.card-header button:hover {
    transition: 0.3s;
    cursor: pointer;
    transform: scale(1.1);
}
.card-header button svg {
    height: 35px;
    width: 35px;
    color: #FFF;
}

.card-content {
    padding-bottom: 20px;
}

.card-content h1 {
    width: 100%;
    margin-top: 15px;
    font-size: 1.9em;
    text-align: center;
}

.card-content .countdown {
    display: flex;
    padding: 20px 30px;
    border-radius: 50px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-top: 10px;
    margin-left: auto;
    margin-right: auto;
}
.card-content .countdown div {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.card-content .countdown div span:first-of-type,
.card-content .countdown div.separator{
    font-size: 25px;
    color: #FFFFFE;
}
div.separator {
    margin: 0 13px;
}
.countdown div span:last-of-type{
    font-size: 10px;
    margin-top: 6px;
}
.countdown div span {
    color: #7f5af0;
}

